<template>
	<div id='activitySet'>
		<div class="top">
			<el-button type="primary" class="selectInput">新增活动</el-button>
			<div>
				<el-input class="selectInput" v-model="userId" placeholder="标题"></el-input>
				<el-button type="primary" class="selectInput">查询</el-button>
			</div>
		</div>

		<div class="bottom">
			<el-table
			:data="tableData"
			height="1050"
			border
			stripe
			lazy
			size="mini"
			row-key="id"
			:tree-props="{children: 'children', hasChildren: 'hasChildren'}">
				<el-table-column
					class="tableClass"
					align='center'
					prop="id"
					label="类型"
					width="150">
				</el-table-column>
				<el-table-column
					class="tableClass"
					align='center'
					prop="name"
					label="标题">
				</el-table-column>
				<el-table-column
					class="tableClass"
					align='center'
					prop="amount1"
					label="交易单号">
				</el-table-column>
				<el-table-column
					class="tableClass"
					align='center'
					prop="amount2"
					label="说明">
				</el-table-column>
				<el-table-column
					class="tableClass"
					align='center'
					prop="amount3"
					label="开启时间">
				</el-table-column>
				<el-table-column
					class="tableClass"
					align='center'
					prop="amount3"
					label="关闭时间">
				</el-table-column>
				<el-table-column
					class="tableClass"
					align='center'
					prop="amount3"
					label="奖励内容">
				</el-table-column>
				<el-table-column
					class="tableClass"
					align='center'
					prop="amount3"
					label="状态">
				</el-table-column>
				<el-table-column
					class="tableClass"
					align='center'
					prop="amount3"
					label="详情">
					<template slot-scope="scope">
						<el-button @click="handleClick(scope.row)" type="text" size="small">详情</el-button>
					</template>
				</el-table-column>
			</el-table>
		</div>

		<div class="pagination">
			<pagination @handleSizeChange="handleSizeChange" @handleCurrentChange="handleCurrentChange" :total="251"></pagination>
		</div>
	</div>
</template>

<script>

import { regionData, CodeToText } from "element-china-area-data";
import pagination from "../../../../../components/pagination"
export default {
	name:'activitySet',
	components:{
		pagination
	},
	data(){
		return{
			dialog:'',
			huahaiNumber:'',
			userName:'',
			userId:'',
			phoneNumber:'',
			state:'',
			startTime:'',
			endTime:'',
			dialogFormVisible:false,
			areaSelectData:regionData,
			selectedOptions:[],
			selectCommand:'待审核',
			tableData: [
				{
					id: '12987123',
					name: '王小虎',
					amount1: '165',
					amount2: '4.43',
					amount3: 12
				},
				{
					id: '12987124',
					name: '王小虎',
					amount1: '324',
					amount2: '1.9',
					amount3: 9
				},
				{
					id: '12987125',
					name: '王小虎',
					amount1: '621',
					amount2: '2.2',
					amount3: 17
				}
			]
		}
	},

	created(){

	},

	methods:{
		handleClick(row){
			this.dialog = row;
			this.$router.push("/userInfo")
		},
		handleSizeChange(val) {
			console.log(`每页 ${val} 条`);
		},
		handleCurrentChange(val) {
			console.log(`当前页: ${val}`);
		},

		handleChange() {
			let loc = "";
			for (let i = 0; i < this.selectedOptions.length; i++) {
				loc += CodeToText[this.selectedOptions[i]];
			}
		},

		handleCommand(command){
			this.selectCommand = command;
		},
	}
}
</script>

<style scoped>

	.top{
		margin-top:-10px;
		height:60px;
		display: flex;
		justify-content: space-between;
	}

	.bottom{
		overflow: scroll;
	}

	.selectInput{
		width: 250px !important;
		margin-right: 30px;
		margin-top: 10px;
		height: 40px;
	}

	.pagination{
		width: 100%;
		display: flex;
		justify-content: center;
		margin-top: 10px;
	}

</style>
